/*TEMP*/
.page-template-page-coming-soon
    #main-header
        display: none
.page-template-page-coming-soon
    .mc4wp-response
        display: none
    .wpcf7-response-output
        text-align: center
        border-color: #1A2338!important
        color: #FFFFFF
        font-family: $montserrat-font
        font-size: 14px
        font-weight: 400
        padding: 8px 0!important
        text-transform: uppercase

.coming-soon
    &__col
        max-width: 800px
    &__col-countdown
        position: absolute
        top: -61px
    &__half
        margin: 0
        min-height:50vh
        align-items: center
        justify-content: center
        flex-direction: column
    &__logo
        display: block
        margin: 0 auto
        max-width: 292px
    &__tophalf
        position: relative
        background-color: #1A2338
        padding: 62px 0
    &__bothalf
        position: relative
        background-color: #C26538
        padding: 128px 0
    &__toptext
        font-family: $acumin-font
        font-weight: 600
        font-size: 34px
        padding: 50px 0
        color: #F3F3F3
        text-align: center
    &__bottext
        font-family: $acumin-font 
        font-size: 22px
        font-weight: 300
        line-height: 40px
        color: #F3F3F3
        text-align: center
    &__newsletter
        display: flex
        justify-content: center
        text-align: center
        margin-top: 40px
        input[type=email]
            margin-right: 20px
            width: 270px
            border-radius: 24px!important
            color: #1A2338
            background-color: #FFFFFF
            border: none!important
            text-transform: uppercase
            font-family: $montserrat-font
            font-size: 12px
            font-weight: 600
            padding: 8.5px 15px!important
        button[type=submit]
            width: 160px
            border-radius: 24px !important
            background-color: #1A2338 !important
            color: #FFFFFF !important
            border: none !important
            text-transform: uppercase
            font-family: $montserrat-font
            font-size: 12px
            font-weight: 600
            padding: 8.5px 0 !important
            margin: 0 0 0 20px !important
        label
            display: none
        form 
            table
                display: flex
                justify-content: center
                tr:nth-child(1)
                    max-width: 270px
                    float: left
                tr:nth-child(2)
                    float: left
            h2
                font-size: 16px
                font-family: $acumin-font
                font-weight: 400
                color: #1A2338
                background-color: #f3f3f3
                border-radius: 6px
                border: 1px solid #1A2338
                padding: 16px
                margin-bottom: 30px

.countdown-section
    .container
        max-width: 555px
        text-align: center
        padding: 0
    &__box
        font-family: $acumin-font
        text-transform: uppercase
        font-size: 12px
        font-weight: 400
    &__circlebox
        width: 122px
        height: 122px
        border:2px solid #C26538
        background-color: #1A2338
        border-radius: 50%
        margin-bottom: 10px
        span
            font-family: $acumin-font
            font-size: 54px
            line-height: 108px
            font-weight: 600
            color: #F3F3F3
            display: inline-block

@include media-breakpoint-down(sm)
    .coming-soon
        &__col-countdown
            position: relative
            margin-top: -130px
        &__newsletter
            form
                table
                    flex-direction: column
            input[type=email]
                margin: 0 0 15px 0
                width: 100%
            button[type=submit]
                margin: 0
                width: 100%
                min-width: 270px !important
                margin-left: 0 !important

    .countdown-section
        .col-6
            display: flex
            justify-content: center
            margin-bottom: 15px